.movie-card {
  background-color: #535353;
  display: flex;
  flex-direction: column;
  height: 222px;
  margin: 20px 20px 0 20px;
  padding: 20px;
  text-align: left;
  width: 210px;
}

h3 {
  color: #000000;
  font-size: 2rem;
  margin: 0 0 10px 0;
}

h4 {
  font-weight: lighter;
  margin: 5px;
}

h5 {
  color: #000000;
}

.bold-text {
  font-weight: bold;
}

.view-charac {
  height: 60px;
  background-color: #EEDB00;
  display: flex;
  justify-content: center;
  margin: 0 20px 20px 20px;
}

.view-charac img {
  margin-top: 5%;
  width: 40px;
  height: 40px;
}

.link {
  text-decoration: none;
}
